import type { FC, ReactNode, MouseEvent, TouchEvent } from 'react'
import { cn } from '@utils/ui'
import CLASS_NAME from './styles'
import Loader from '@UI/Loader'
import { createRipple } from '@utils/ripple'

export interface ButtonProps {
  className?: string
  color?: 'primary' | 'secondary'
  dataTestId?: string
  endAdornment?: ReactNode
  isContentSized?: boolean
  isDisabled?: boolean
  isLoading?: boolean
  isRippleDisabled?: boolean
  onClick: (e?: MouseEvent) => void
  startAdornment?: ReactNode
  variant?: 'contained' | 'outlined' | 'text'
}

export const Button: FC<ButtonProps> = ({
  children,
  color = 'primary',
  className,
  dataTestId,
  endAdornment,
  isContentSized,
  isDisabled,
  isLoading,
  isRippleDisabled,
  onClick: _onClick,
  startAdornment,
  variant = 'contained'
}) => {
  const rippleMode = (
    isRippleDisabled === true || isDisabled === true || isLoading === true || variant === 'text'
  )
    ? 'none'
    : variant === 'contained' ? 'light' : 'dark'

  const onClick = (e: MouseEvent<HTMLElement>): void => {
    if (isDisabled === true || isLoading === true) {
      return
    }

    _onClick(e)
  }

  const onMouseDown = (e: MouseEvent<HTMLElement> | TouchEvent<HTMLElement>): void => {
    if (rippleMode !== 'none') {
      createRipple(e)
    }
  }

  return (
    <button
      className={cn(
        CLASS_NAME,
        className,
        `cf-ripple-container-${rippleMode}`,
        `cf-button-variant-${variant}`,
        `cf-button-color-${color}`,
        isContentSized === true ? 'cf-button-content-sized' : '',
        isDisabled === true ? 'cf-button-disabled' : '',
        isLoading === true ? 'cf-button-loading' : '',
        'cf-button-root'
      )}
      data-test-id={dataTestId}
      onClick={onClick}
      onMouseDown={onMouseDown}
      onTouchStart={onMouseDown}
    >
      {
        (isLoading === true && variant !== 'text')
          ? <Loader className='cf-button-loader' />
          : (
            <>
              <span className='cf-button-start-adornment'>
                {startAdornment != null ? startAdornment : null}
              </span>
              <span className='cf-button-content'>
                {children}
                {(isLoading === true && variant === 'text') && (
                  <Loader className='cf-button-loader' />
                )}
              </span>
              <span className='cf-button-end-adornment'>
                {endAdornment != null ? endAdornment : null}
              </span>
            </>
            )
      }
    </button>
  )
}

export default Button
